.header_container{
  padding: 2%;
  border-bottom-style: solid;
}

.logo{
  width: 100%;
  padding: 5%;
  margin: 1%;
}

.title{
  font-style: normal;
  font-weight: bolder;
  font-family: Verdana;
  color: Red;
  margin: 1%;
}

.header_subtitle{
  font-style: italic;
  font-size: x-large;
  color: mediumseagreen;
}

.fruits{
