  background-color: rgb(255,190,166);
}

.product_image{
  width: 320px;
  height: 320px;
  padding: 2%;
}

.product_card{
  float: left;
  width: auto;
  height: 500px;
  padding: 3%;
}

.button_products{
  margin: 30px;
  padding: 10px;
}
